body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.calendar {
  flex-wrap: wrap;
  gap: 1px;
  width: 706px;
  padding: 10px;
  display: flex;
}

.calendar__day {
  background-color: #eee;
  transition: transform .5s, background-color .5s;
}

.calendar__day:hover {
  cursor: pointer;
  background-color: #ffbfcb;
  transform: translateY(-20px);
}

.calendar--month-length-28 .calendar__day:first-child:before {
  box-sizing: border-box;
  content: "1";
  border: 1px solid #000;
  justify-content: center;
  align-items: center;
  width: 100px;
  height: 100px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
